import React, { useState } from "react";
import "../styles/StudentsProfileCard.css";

import { StudentsData } from "../helpers/StudentsData.js";

import { Button, Modal } from "react-bootstrap";
import CloseIcon from "@material-ui/icons/Close";

function SampleStudentsProfileCard() {
	const [isModalOpen, setOpenModal] = useState(false);

	let [studentUniqueKey, updateStudentUniqueKey] = useState(undefined);

	function toggleModal() {
		setOpenModal(!isModalOpen);
	}

	return (
		<div>
			<div className="student-profile-card-wrapper">
				{StudentsData.map((student, key) => {
					return (
						<div className="student-profile-card" key={key}>
							<div className="student-profile-image-container">
								<img src={student.image} alt="" />
							</div>
							<div className="student-name-and-skill-container">
								<small>
									<strong>Name:</strong> {student.name}
								</small>
								<small>
									<strong>Skill:</strong> {student.skill}
								</small>
								<button
									onClick={function () {
										updateStudentUniqueKey(
											(studentUniqueKey = key)
										);
										toggleModal();
									}}
								>
									<small>
										<u>See more</u>
									</small>
								</button>
							</div>
						</div>
					);
				})}
			</div>

			{StudentsData.map((student, key) => {
				return (
					<Modal
						key={key}
						dialogClassName="myy-modal"
						show={isModalOpen && key === studentUniqueKey}
						onHide={toggleModal}
						centered
					>
						<Modal.Header>
							<h5>{student.name}</h5>
							<Button
								onClick={toggleModal}
								style={{
									backgroundColor: "transparent",
									color: "black",
									border: "none",
								}}
							>
								<CloseIcon />
							</Button>
						</Modal.Header>

						<Modal.Body className="modal-body">
							<div className="image-container">
								<img src={student.image} alt="" />
							</div>
							<div
								className="details"
								style={{ marginTop: "30px" }}
							>
								<div>
									<p>
										<strong>Gender: </strong>
										{student.gender}
									</p>
									<p>
										<strong>Skill: </strong>
										{student.skill}
									</p>
									<p>
										<strong>Skill Level: </strong>
										{student.skillLevel}
									</p>
									<p>
										<strong>Phone: </strong>
										{student.phone}
									</p>
									<p>
										<strong>Email: </strong>
										{student.email}
									</p>
									<p>
										<strong>Hobby: </strong>
										{student.hobby}
									</p>
								</div>
							</div>
						</Modal.Body>

						<Modal.Footer></Modal.Footer>
					</Modal>
				);
			})}
		</div>
	);
}

export default SampleStudentsProfileCard;
